.about-us {
  padding: 30px 0 100px 0;
}

.about-us__title {
  margin: 0 0 14px;
}

.about-us__description {
  margin: 0 0 45px;
}

.employee-container {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.employee {
  padding: 35px 0;
  margin-right: 64px;
  position: relative;
  width: calc(30% - 32px);
}

.employee__name {
  margin: 0;
}

.employee__title {
  margin: 0;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: normal;
  line-height: 1.6rem;
}

.employee__contact {
  font-size: 1.6rem;
  font-style: normal;
  font-weight: normal;
  line-height: 1.6rem;
  color: #1943ef;
  text-decoration: none;
}

.employee__contact:hover {
  text-decoration: underline;
}

.employee__photo {
  width: 98px;
  height: 98px;
  border-radius: 50%;
  position: absolute;
  right: -32px;
  top: 20px;
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.15);
}